import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import BaseTemplate from '../../templates/BaseTemplate'
import './about.css'

const About = ({ data }) => {

    const { section } = useParams()

    const [mode, setMode] = useState('politics')

    const handleOnClick = (param) => {
        setMode(param)
    }

    useEffect(() => {
        handleOnClick(section)
    }, [])




    return (
        <BaseTemplate mostViewed={data}>
            <div className='container  p-0'>
                <div className='d-flex flex-column flex-lg-row bg-light'>
                    <div className='border-end w-auto'>
                        <ul className="nav flex-column">
                            <li className="nav-item border-bottom">
                                <a className="nav-link fs-6 text-dark active" aria-current="page" onClick={() => handleOnClick('politics')} href="#">Poltica de privacidad</a>
                            </li>
                            <li className="nav-item border-bottom">
                                <a className="nav-link fs-6 text-dark" href="#" onClick={() => handleOnClick('conditions')}>Condiciones de uso</a>
                            </li>
                            <li className="nav-item border-bottom">
                                <a className="nav-link fs-6 text-dark" href="#" onClick={() => handleOnClick('contact')}>Contacto</a>
                            </li>
                            <li className="nav-item border-bottom">
                                <a className="nav-link fs-6 text-dark" href="#" onClick={() => handleOnClick('cookies')}>Poltica de cookies</a>
                            </li>
                            <li className="nav-item border-bottom">
                                <a className="nav-link fs-6 text-dark" href="#" onClick={() => handleOnClick('aboutus')}>Quines somos?</a>
                            </li>

                        </ul>
                    </div>
                    <div className='container w-100 p-2'>
                        {
                            evaluate(mode)
                        }
                    </div>

                </div>
            </div>
        </BaseTemplate>
    )
}



const evaluate = (type) => {
    let component
    switch (type) {
        case 'politics':
            component = <Politics />
            break;
        case 'conditions':
            component = <Conditions />
            break;
        case 'aboutus':
            component = <AboutUs />
            break;
        case 'contact':
            component = <Contact />
            break;
        case 'cookies':
            component = <Cookies />
            break;
        default:
            component = <Politics />
            break;
    }

    return component
}

